import { Grid, TextField } from '@material-ui/core';
import { gql, useLazyQuery } from '@apollo/client';
import _get from 'lodash.get';
import { useEffect, useState, ChangeEvent } from 'react';
import { useDebounce } from 'ahooks';
import _isEmpty from 'lodash.isempty';
import { searchDataTreeProperties } from '../../../../../graphql/queries';
import InputLabel from '../../../../../ui-component/extended/Form/InputLabel';
import SearchPropertyList from './SearchPropertyList';
import { PropertyData } from '../../../../../types';
import { useSnackBar } from '../../../../../hooks';

type Props = {
	onSelect: (property: PropertyData) => void;
};

const SearchProperty = (props: Props) => {
	const { onSelect } = props;
	const showSnackBar = useSnackBar();
	const [search, setSearch] = useState<string>('');
	const debouncedSearchTerm = useDebounce(search, { wait: 500 });

	const [doSearch, { loading, error, data }] = useLazyQuery(
		gql(searchDataTreeProperties)
	);

	useEffect(() => {
		if (!debouncedSearchTerm) return;
		doSearch({
			variables: {
				SearchText: search,
			},
		});
	}, [debouncedSearchTerm]);

	useEffect(() => {
		if (!error) return;
		const message = _get(error, 'message', 'Something went wrong');
		showSnackBar({ message });
	}, [error]);

	const handleSearch = async (
		event: ChangeEvent<HTMLTextAreaElement | HTMLInputElement> | undefined
	) => {
		const newString = event?.target.value;
		setSearch(newString || '');
	};
	const properties = _get(data, ['searchDataTreeProperties', 'Properties'], {});
	const isSearchNotFound =
		_isEmpty(properties) && !_isEmpty(debouncedSearchTerm);

	return (
		<>
			<Grid container alignItems='center'>
				<Grid item xs={12} lg={12}>
					<Grid container alignItems='center'>
						<Grid item xs={12} lg={5}>
							<InputLabel sx={{ m: 2 }}>Search for property using address</InputLabel>
						</Grid>
						<Grid item xs={12} lg={7}>
							<TextField variant='outlined' onChange={handleSearch} />
						</Grid>
					</Grid>
				</Grid>
			</Grid>
			<SearchPropertyList
				properties={properties}
				loading={loading}
				onSelect={onSelect}
				isSearchNotFound={isSearchNotFound}
			/>
		</>
	);
};

export default SearchProperty;
